___CSS_LOADER_EXPORT___.push([module.id, `
/* Form layout */
.iframewidget-grid-form[data-v-1d255c5b] {
    display: grid;
    margin-top: 30px;
}
.iframewidget-grid-form label[data-v-1d255c5b]:not(:first-child) {
    margin-top: 20px;
}
input[data-v-1d255c5b] {
    max-width: 400px;
    width: 100%;
}

/* Checkbox styles */
.checkbox-container[data-v-1d255c5b] {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}
.checkbox-container input[type="checkbox"][data-v-1d255c5b] {
    position: static;
    display: block;
    height: 16px;
    width: 16px;
    margin: 0;
    opacity: 1;
    cursor: pointer;
}
.checkbox-container .checkbox-icon[data-v-1d255c5b] {
    display: none;
}
.checkbox-container input[type="checkbox"][data-v-1d255c5b]:checked {
    background-color: var(--color-primary);
    border-color: var(--color-primary);
}

/* Button styles */
.iframewidget-button-group[data-v-1d255c5b] {
    margin-top: 20px;
}

/* Header styles */
.iframewidget-header[data-v-1d255c5b] {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 10px;
}
.iframewidget-logo[data-v-1d255c5b] {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.iframewidget-logo a[data-v-1d255c5b] {
    text-decoration: none;
    color: inherit;
}
.iframewidget-logo a.logo-link[data-v-1d255c5b] {
    display: block;
}
.iframewidget-logo a:hover .iframewidget-version[data-v-1d255c5b] {
    text-decoration: underline;
    color: var(--color-primary-element);
}
.iframewidget-logo-image[data-v-1d255c5b] {
    width: 60px;
    height: 60px;
    border-radius: 5px;
    transition: transform 0.2s ease, box-shadow 0.2s ease;
}
.iframewidget-logo-image[data-v-1d255c5b]:hover {
    transform: scale(1.05);
    filter: drop-shadow(1px -1px 3px rgba(0, 0, 0, 0.5));
}
.iframewidget-version[data-v-1d255c5b] {
    font-size: 12px;
    color: var(--color-text-maxcontrast);
    margin-top: 5px;
}

/* Layout container */
.iframewidget-admin-container[data-v-1d255c5b] {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    margin-top: 20px;
}
.iframewidget-admin-form[data-v-1d255c5b] {
    flex: 0.5;
    min-width: 330px;
}
.iframewidget-admin-preview[data-v-1d255c5b] {
    flex: 1;
    min-width: 320px;
    max-width: 640px;
}

/* Preview styles */
.preview-container[data-v-1d255c5b] {
    border: 1px solid var(--color-border);
    border-radius: var(--border-radius-container-large);
    overflow: hidden;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
    background-color: var(--color-main-background-blur);
    transition: width 0.3s ease;
}
.preview-header[data-v-1d255c5b] {
    display: flex;
    align-items: center;
    padding: 16px;
    background-color: var(--color-main-background-blur);
}
.preview-header h2[data-v-1d255c5b] {
    display: block;
    align-items: center;
    flex-grow: 1;
    margin: 0;
    font-size: 20px;
    font-weight: bold;
    padding: 16px 8px;
    height: 56px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.preview-header span[data-v-1d255c5b]:first-child {
    width: 16px;
    height: 16px;
    background-size: 16px;
    margin-right: 8px;
}
.preview-header span[data-v-1d255c5b] {
    background-size: 32px!important;
    width: 32px!important;
    height: 32px!important;
    background-position: center;
    float: left;
    margin-top: -6px;
}
.preview-content[data-v-1d255c5b] {
    margin: 0 16px 16px 16px;
    position: relative;
    height: 424px;
}
.preview-empty[data-v-1d255c5b] {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 200px;
    padding: 20px;
    color: var(--color-text-maxcontrast);
    text-align: center;
}
.preview-frame[data-v-1d255c5b] {
    width: 100%;
    height: 100%;
    border: none;
    min-height: 50px;
}
.preview-title[data-v-1d255c5b] {
    font-size: 1rem;
    font-weight: bold;
    margin-bottom: 10px;
    margin-top: 0;
}

/* Empty title styles */
.preview-header.preview-title-empty[data-v-1d255c5b] {
    height: 10px !important;
    padding: 0 !important;
}
.preview-header.preview-title-empty h2[data-v-1d255c5b] {
    display: none !important;
}
.preview-content.preview-title-empty[data-v-1d255c5b] {
    height: calc(424px + 78px);
}

/* Custom icon styles */
.preview-header h2 .widget-icon[data-v-1d255c5b] {
    float: left !important;
    display: inline-flex !important;
    margin-right: 16px !important;
    margin-left: 0 !important;
    position: relative !important;
    width: 32px !important;
    height: 32px !important;
}
.preview-header h2 .widget-icon img[data-v-1d255c5b] {
    width: 32px;
    height: 32px;
    object-fit: contain;
    vertical-align: middle;
}

/* Hide default icon when custom icon is present */
.preview-header h2 .widget-icon + .icon-iframewidget[data-v-1d255c5b] {
    display: none !important;
}

/* Icon input styles */
.icon-input-container[data-v-1d255c5b] {
    display: flex;
    align-items: center;
    gap: 15px;
    max-width: 400px!important;
}

/* Icon Finder Link styles */
.icon-finder[data-v-1d255c5b] {
    font-size: 0.85em;
    font-weight: normal;
    color: var(--color-text-maxcontrast);
}
.icon-finder a[data-v-1d255c5b] {
    color: var(--color-primary);
}

/* Color button container transitions */
.color-button-container[data-v-1d255c5b] {
    display: inline-flex;
    transition: width 0.3s ease, margin 0.3s ease;
    width: 0;
    height: 32px;
}
.color-button-container.has-button[data-v-1d255c5b] {
    width: 32px;
    margin-left: 4px;
}

/* Color picker styles */
input[type="color"][data-v-1d255c5b] {
    aspect-ratio: 1/1;
    min-width: 32px;
    min-height: 32px;
    width: 32px;
    height: 32px;
    padding: 0!important;
    margin: 0!important;
    border-radius: 50%;
    box-shadow: 0 0 5px rgba(255, 255, 255, 0.5) !important;
    transition: transform 0.2s, box-shadow 0.2s;
    cursor: pointer!important;
}
input[type="color"][data-v-1d255c5b]:hover {
    transform: scale(1.1);
    transition: transform 0.2s, box-shadow 0.2s;
    box-shadow: 0 0 12px rgba(255, 255, 255, 0.5) !important;
}
input[type="color"][data-v-1d255c5b]::-webkit-color-swatch-wrapper {
    padding: 0;
}
input[type="color"][data-v-1d255c5b]::-webkit-color-swatch {
    border: none;
    border-radius: 50%;
}

/* Firefox support */
input[type="color"][data-v-1d255c5b]::-moz-color-swatch {
    border: none;
    border-radius: 50%;
}

/* Color reset button transitions */
.icon-reset-color[data-v-1d255c5b] {
    min-width: 32px;
    min-height: 32px;
    background-color: transparent;
    border: none;
    cursor: pointer;
    opacity: 0.7;
    margin: 0!important;
    border-radius: 50%;
}
.icon-reset-color[data-v-1d255c5b]:hover {
    opacity: 1;
    background-color: var(--color-background-hover);
}
.fade-scale-enter-from[data-v-1d255c5b] {
    opacity: 0;
    transform: scale(0);
}
.fade-scale-enter-active[data-v-1d255c5b], 
.fade-scale-leave-active[data-v-1d255c5b] {
    transition: all 0.3s ease;
}
.fade-scale-enter-from[data-v-1d255c5b],
.fade-scale-leave-to[data-v-1d255c5b] {
    opacity: 0;
    transform: scale(0);
}
.icon-preview[data-v-1d255c5b] {
    display: inline-flex;
    align-items: center;
    margin: 0 4px;
}

/* CSP error display styling */
.widget-error.csp-error[data-v-1d255c5b] {
    display: flex;
    flex-direction: column;
    padding: 16px;
    text-align: center;
    height: 200px;
    justify-content: center;
}

/* Error title styling */
.widget-error .error-title[data-v-1d255c5b] {
    font-weight: bold;
    margin-bottom: 8px;
    color: var(--color-text-maxcontrast);
}

/* Error message paragraph styling */
.widget-error p[data-v-1d255c5b] {
    margin: 8px 0;
    color: var(--color-text-maxcontrast);
}

/* Container for action buttons in error state */
.error-actions[data-v-1d255c5b] {
    margin-top: 16px;
}

/* Button styling for error actions */
.error-actions .button[data-v-1d255c5b] {
    display: inline-block;
    padding: 8px 16px;
    text-decoration: none;
    border-radius: var(--border-radius);
    background-color: var(--color-primary);
    color: var(--color-primary-text);
}

/* Hover state for action buttons */
.error-actions .button[data-v-1d255c5b]:hover {
    background-color: var(--color-primary-element-hover);
}

`, "",{"version":3,"sources":["webpack://./src/components/PersonalSettings.vue"],"names":[],"mappings":";AAkYA,gBAAA;AACA;IACA,aAAA;IACA,gBAAA;AACA;AAEA;IACA,gBAAA;AACA;AAEA;IACA,gBAAA;IACA,WAAA;AACA;;AAEA,oBAAA;AACA;IACA,aAAA;IACA,sBAAA;IACA,uBAAA;AACA;AAEA;IACA,gBAAA;IACA,cAAA;IACA,YAAA;IACA,WAAA;IACA,SAAA;IACA,UAAA;IACA,eAAA;AACA;AAEA;IACA,aAAA;AACA;AAEA;IACA,sCAAA;IACA,kCAAA;AACA;;AAEA,kBAAA;AACA;IACA,gBAAA;AACA;;AAEA,kBAAA;AACA;IACA,aAAA;IACA,8BAAA;IACA,uBAAA;IACA,mBAAA;AACA;AAEA;IACA,aAAA;IACA,sBAAA;IACA,mBAAA;AACA;AAEA;IACA,qBAAA;IACA,cAAA;AACA;AAEA;IACA,cAAA;AACA;AAEA;IACA,0BAAA;IACA,mCAAA;AACA;AAEA;IACA,WAAA;IACA,YAAA;IACA,kBAAA;IACA,qDAAA;AACA;AAEA;IACA,sBAAA;IACA,oDAAA;AACA;AAEA;IACA,eAAA;IACA,oCAAA;IACA,eAAA;AACA;;AAEA,qBAAA;AACA;IACA,aAAA;IACA,eAAA;IACA,SAAA;IACA,gBAAA;AACA;AAEA;IACA,SAAA;IACA,gBAAA;AACA;AAEA;IACA,OAAA;IACA,gBAAA;IACA,gBAAA;AACA;;AAEA,mBAAA;AACA;IACA,qCAAA;IACA,mDAAA;IACA,gBAAA;IACA,wCAAA;IACA,mDAAA;IACA,2BAAA;AACA;AAEA;IACA,aAAA;IACA,mBAAA;IACA,aAAA;IACA,mDAAA;AACA;AAEA;IACA,cAAA;IACA,mBAAA;IACA,YAAA;IACA,SAAA;IACA,eAAA;IACA,iBAAA;IACA,iBAAA;IACA,YAAA;IACA,mBAAA;IACA,gBAAA;IACA,uBAAA;AACA;AAEA;IACA,WAAA;IACA,YAAA;IACA,qBAAA;IACA,iBAAA;AACA;AAEA;IACA,+BAAA;IACA,qBAAA;IACA,sBAAA;IACA,2BAAA;IACA,WAAA;IACA,gBAAA;AACA;AAEA;IACA,wBAAA;IACA,kBAAA;IACA,aAAA;AACA;AAEA;IACA,aAAA;IACA,uBAAA;IACA,mBAAA;IACA,aAAA;IACA,aAAA;IACA,oCAAA;IACA,kBAAA;AACA;AAEA;IACA,WAAA;IACA,YAAA;IACA,YAAA;IACA,gBAAA;AACA;AAEA;IACA,eAAA;IACA,iBAAA;IACA,mBAAA;IACA,aAAA;AACA;;AAEA,uBAAA;AACA;IACA,uBAAA;IACA,qBAAA;AACA;AAEA;IACA,wBAAA;AACA;AAEA;IACA,0BAAA;AACA;;AAEA,uBAAA;AACA;IACA,sBAAA;IACA,+BAAA;IACA,6BAAA;IACA,yBAAA;IACA,6BAAA;IACA,sBAAA;IACA,uBAAA;AACA;AAEA;IACA,WAAA;IACA,YAAA;IACA,mBAAA;IACA,sBAAA;AACA;;AAEA,kDAAA;AACA;IACA,wBAAA;AACA;;AAEA,sBAAA;AACA;IACA,aAAA;IACA,mBAAA;IACA,SAAA;IACA,0BAAA;AACA;;AAEA,4BAAA;AACA;IACA,iBAAA;IACA,mBAAA;IACA,oCAAA;AACA;AAEA;IACA,2BAAA;AACA;;AAEA,uCAAA;AACA;IACA,oBAAA;IACA,6CAAA;IACA,QAAA;IACA,YAAA;AACA;AAEA;IACA,WAAA;IACA,gBAAA;AACA;;AAEA,wBAAA;AACA;IACA,iBAAA;IACA,eAAA;IACA,gBAAA;IACA,WAAA;IACA,YAAA;IACA,oBAAA;IACA,mBAAA;IACA,kBAAA;IACA,uDAAA;IACA,2CAAA;IACA,yBAAA;AACA;AAEA;IACA,qBAAA;IACA,2CAAA;IACA,wDAAA;AACA;AAEA;IACA,UAAA;AACA;AAEA;IACA,YAAA;IACA,kBAAA;AACA;;AAEA,oBAAA;AACA;IACA,YAAA;IACA,kBAAA;AACA;;AAEA,mCAAA;AACA;IACA,eAAA;IACA,gBAAA;IACA,6BAAA;IACA,YAAA;IACA,eAAA;IACA,YAAA;IACA,mBAAA;IACA,kBAAA;AACA;AAEA;IACA,UAAA;IACA,+CAAA;AACA;AAEA;IACA,UAAA;IACA,mBAAA;AACA;AAEA;;IAEA,yBAAA;AACA;AAEA;;IAEA,UAAA;IACA,mBAAA;AACA;AAEA;IACA,oBAAA;IACA,mBAAA;IACA,aAAA;AACA;;AAEA,8BAAA;AACA;IACA,aAAA;IACA,sBAAA;IACA,aAAA;IACA,kBAAA;IACA,aAAA;IACA,uBAAA;AACA;;AAEA,wBAAA;AACA;IACA,iBAAA;IACA,kBAAA;IACA,oCAAA;AACA;;AAEA,oCAAA;AACA;IACA,aAAA;IACA,oCAAA;AACA;;AAEA,gDAAA;AACA;IACA,gBAAA;AACA;;AAEA,qCAAA;AACA;IACA,qBAAA;IACA,iBAAA;IACA,qBAAA;IACA,mCAAA;IACA,sCAAA;IACA,gCAAA;AACA;;AAEA,mCAAA;AACA;IACA,oDAAA;AACA","sourcesContent":["<template>\r\n    <div id=\"iframewidget-personal-settings\" class=\"section\">\r\n        <!-- Header section -->\r\n        <div class=\"iframewidget-header\">\r\n            <h2>{{ t('iframewidget', 'Personal iFrame Widget Settings') }}</h2>\r\n            <div class=\"iframewidget-logo\">\r\n                <a href=\"https://github.com/IT-BAER\" target=\"_blank\" rel=\"noopener noreferrer\" class=\"logo-link\">\r\n                    <img src=\"../../img/baer4-100x100.png\" alt=\"Logo\" class=\"iframewidget-logo-image\">\r\n                </a>\r\n                <a href=\"https://github.com/IT-BAER/nc-iframewidget/releases\" target=\"_blank\" rel=\"noopener noreferrer\" class=\"version-link\">\r\n                    <span class=\"iframewidget-version\">v0.7.7</span>\r\n                </a>\r\n            </div>\r\n        </div>\r\n        \r\n        <p class=\"settings-hint\">\r\n            {{ t('iframewidget', 'Configure your personal iFrame Widget for the Dashboard.') }}\r\n        </p>\r\n        \r\n        <div class=\"iframewidget-admin-container\">\r\n            <!-- Left side: Settings form -->\r\n            <div class=\"iframewidget-admin-form\">\r\n                <div class=\"iframewidget-grid-form\">\r\n                    <!-- Widget Title -->\r\n                    <label for=\"personal-iframe-widget-title\">\r\n                        {{ t('iframewidget', 'Widget Title') }}\r\n                    </label>\r\n                    <input id=\"personal-iframe-widget-title\"\r\n                        v-model=\"state.widgetTitle\"\r\n                        type=\"text\"\r\n                        placeholder=\"Hidden\">\r\n                    \r\n                    <!-- Widget Icon -->\r\n                    <label for=\"personal-widget-icon\">\r\n                        {{ t('iframewidget', 'Widget Icon') }}\r\n                        <span class=\"icon-finder\">\r\n                            - {{ t('iframewidget', 'Find icons at') }} \r\n                            <a href=\"https://simpleicons.org/\" \r\n                               target=\"_blank\" \r\n                               rel=\"noopener noreferrer\">\r\n                               simpleicons.org\r\n                            </a>\r\n                        </span>\r\n                    </label>\r\n                    <div class=\"icon-input-container\">\r\n                        <input id=\"personal-widget-icon\"\r\n                            v-model=\"typedIcon\"\r\n                            type=\"text\"\r\n                            @input=\"debounceIconUpdate\"\r\n                            style=\"max-width: 350px!important;\"\r\n                            placeholder=\"si:github or si:nextcloud\">\r\n                        <input type=\"color\" \r\n                               :value=\"colorValue\" \r\n                               @input=\"updateColor\" \r\n                               :disabled=\"!state.widgetIcon || !state.widgetIcon.startsWith('si:')\"\r\n                               class=\"color-picker\">\r\n                        <div class=\"color-button-container\" :class=\"{'has-button': state.widgetIconColor && state.widgetIconColor !== ''}\">\r\n                            <transition name=\"fade-scale\">\r\n                                <button v-if=\"state.widgetIconColor && state.widgetIconColor !== ''\" \r\n                                        type=\"button\"\r\n                                        class=\"icon-delete icon-reset-color\" \r\n                                        @click=\"clearColor\"\r\n                                        :title=\"t('iframewidget', 'Reset color')\">\r\n                                </button>\r\n                            </transition>\r\n                        </div>\r\n                    </div>\r\n\r\n                    <!-- iFrame URL -->\r\n                    <label for=\"personal-iframe-url\">\r\n                        {{ t('iframewidget', 'URL to Display') }}\r\n                    </label>\r\n                    <input id=\"personal-iframe-url\"\r\n                        :value=\"typedUrl\"\r\n                        @input=\"handleUrlInput\"\r\n                        type=\"text\"\r\n                        class=\"iframewidget-input\"\r\n                        placeholder=\"https://example.org\">\r\n\r\n                    <!-- iFrame Height -->\r\n                    <label for=\"personal-iframe-height\">\r\n                        {{ t('iframewidget', 'iFrame Height (px)') }}\r\n                    </label>\r\n                    <input id=\"personal-iframe-height\"\r\n                        v-model=\"state.iframeHeight\"\r\n                        type=\"number\"\r\n                        min=\"0\"\r\n                        placeholder=\"100%\">\r\n\r\n                    <!-- Extra Wide Toggle -->\r\n                    <label for=\"personal-extra-wide\" class=\"checkbox-label\">\r\n                        {{ t('iframewidget', 'Extra Wide (2 Col)') }}\r\n                    </label>\r\n                    <div class=\"checkbox-container\">\r\n                        <label>\r\n                            <input id=\"personal-extra-wide\"\r\n                                v-model=\"extraWideComputed\"\r\n                                type=\"checkbox\"\r\n                                class=\"checkbox\">\r\n                            <span class=\"checkbox-icon\"></span>\r\n                        </label>\r\n                    </div>\r\n                </div>\r\n\r\n                <!-- Save Button -->\r\n                <div class=\"iframewidget-button-group\">\r\n                    <button @click=\"saveSettings\" \r\n                            :disabled=\"saving\"\r\n                            class=\"primary\">\r\n                        <span v-if=\"!saving\">{{ t('iframewidget', 'Save') }}</span>\r\n                        <span v-else class=\"icon icon-loading-small\"></span>\r\n                    </button>\r\n                </div>\r\n            </div>\r\n\r\n            <!-- Right side: Widget preview -->\r\n            <div class=\"iframewidget-admin-preview\">\r\n                <h4 class=\"preview-title\">{{ t('iframewidget', 'Preview') }}</h4>\r\n                <div class=\"preview-container\" :style=\"{ width: isExtraWide ? '640px' : '320px' }\">\r\n                    <div class=\"preview-header\" :class=\"{'preview-title-empty': !state.widgetTitle || state.widgetTitle.trim() === ''}\">\r\n                        <h2>\r\n                            <span v-if=\"state.widgetIcon && state.widgetTitle && state.widgetTitle.trim() !== ''\"\r\n                                  class=\"widget-icon\">\r\n                                <img :src=\"getIconUrl(state.widgetIcon, state.widgetIconColor)\" \r\n                                     :alt=\"state.widgetIcon\" \r\n                                     @error=\"handleIconError\"\r\n                                     class=\"dashboard-icon\">\r\n                            </span>\r\n                            <span class=\"icon-iframewidget\" v-else-if=\"state.widgetTitle && state.widgetTitle.trim() !== ''\"></span>\r\n                            <span v-if=\"state.widgetTitle && state.widgetTitle.trim() !== ''\">{{ state.widgetTitle }}</span>\r\n                        </h2>\r\n                    </div>\r\n                    <div class=\"preview-content\" :class=\"{'preview-title-empty': !state.widgetTitle || state.widgetTitle.trim() === ''}\">\r\n                        <div v-if=\"!state.iframeUrl\" class=\"preview-empty\">\r\n                            {{ t('iframewidget', 'No URL configured. Please set a URL in the Settings.') }}\r\n                        </div>\r\n\r\n                        <!-- CSP Error state -->\r\n                        <div v-else-if=\"iframeError && state.iframeUrl\" class=\"widget-error csp-error\">\r\n                            <div class=\"error-title\">{{ t('iframewidget', 'Failed to load content') }}</div>\r\n                            <p>{{ t('iframewidget', 'This might be caused by Content Security Policy (CSP) restrictions.') }}</p>\r\n                            <div class=\"error-actions\">\r\n                                <a href=\"https://github.com/IT-BAER/nc-iframewidget#csp-configuration\" target=\"_blank\" class=\"button primary\">\r\n                                    {{ t('iframewidget', 'View CSP Configuration Guide') }}\r\n                                </a>\r\n                            </div>\r\n                        </div>\r\n                        <iframe v-else\r\n                                :src=\"state.iframeUrl\"\r\n                                :style=\"{ height: previewHeight }\"\r\n                                class=\"preview-frame\"\r\n                                referrerpolicy=\"no-referrer\"\r\n                                allow=\"fullscreen\"\r\n                                @error=\"handleIframeError\"\r\n                                @load=\"iframeError = false\"\r\n                                sandbox=\"allow-same-origin allow-scripts allow-popups allow-forms\">\r\n                        </iframe>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n        </div>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nimport axios from '@nextcloud/axios'\r\nimport { generateUrl } from '@nextcloud/router'\r\nimport { loadState } from '@nextcloud/initial-state'\r\nimport { showSuccess, showError } from '@nextcloud/dialogs'\r\nimport debounce from 'debounce'\r\n\r\nexport default {\r\n    name: 'PersonalSettings',\r\n    data() {\r\n        return {\r\n            state: {\r\n                widgetTitle: '',\r\n                widgetIcon: '',\r\n                widgetIconColor: '',\r\n                iframeUrl: '',\r\n                iframeHeight: '',\r\n                extraWide: false,\r\n            },\r\n            saving: false,\r\n            typedIcon: '',\r\n            typedUrl: '',\r\n            urlUpdateTimer: null,\r\n            iconUpdateTimer: null,\r\n            iframeError: false,\r\n            originalState: null\r\n        }\r\n    },\r\n    computed: {\r\n        isExtraWide() {\r\n            return this.state.extraWide === true || this.state.extraWide === 'true'\r\n        },\r\n        extraWideComputed: {\r\n            get() {\r\n                return this.state.extraWide === true;\r\n            },\r\n            set(value) {\r\n                this.state.extraWide = value;\r\n                this.$nextTick(() => {\r\n                    const container = this.$el.querySelector('.preview-container');\r\n                    if (container) {\r\n                        container.style.width = value ? '640px' : '320px';\r\n                    }\r\n                });\r\n            }\r\n        },\r\n        colorValue() {\r\n            if (!this.state.widgetIcon || !this.state.widgetIcon.startsWith('si:')) {\r\n                return '#ffffff'\r\n            }\r\n            return this.state.widgetIconColor || '#ffffff'\r\n        },\r\n        previewHeight() {\r\n            if (!this.state.iframeHeight || this.state.iframeHeight === '0') {\r\n                return '100%';\r\n            } else {\r\n                return parseInt(this.state.iframeHeight) + 'px';\r\n            }\r\n        }\r\n    },\r\n    watch: {\r\n        'state.widgetIconColor': {\r\n            handler(newColor) {\r\n                if (this.state.widgetIcon && this.state.widgetIcon.startsWith('si:')) {\r\n                    const iconImage = this.$el?.querySelector('.widget-icon img');\r\n                    if (iconImage) {\r\n                        iconImage.src = this.getIconUrl(this.state.widgetIcon, newColor);\r\n                    }\r\n                }\r\n            },\r\n            immediate: true\r\n        }\r\n    },\r\n    created() {\r\n        try {\r\n            const config = loadState('iframewidget', 'personal-iframewidget-config')\r\n            this.state = { ...config }\r\n            this.originalState = { ...config }\r\n            this.typedUrl = this.state.iframeUrl\r\n            this.typedIcon = this.state.widgetIcon\r\n        } catch (e) {\r\n            console.error('Failed to load personal widget config:', e)\r\n            showError(t('iframewidget', 'Failed to load settings'))\r\n        }\r\n    },\r\n    mounted() {\r\n        this.$nextTick(() => {\r\n            const container = this.$el.querySelector('.preview-container');\r\n            if (container && (this.state.extraWide === true)) {\r\n                container.style.width = '640px';\r\n            }\r\n        });\r\n\r\n        window.addEventListener('securitypolicyviolation', this.handleCSPViolation);\r\n    },\r\n    beforeDestroy() {\r\n        if (this.urlUpdateTimer) {\r\n            clearTimeout(this.urlUpdateTimer);\r\n        }\r\n        if (this.iconUpdateTimer) {\r\n            clearTimeout(this.iconUpdateTimer);\r\n        }\r\n        window.removeEventListener('securitypolicyviolation', this.handleCSPViolation);\r\n    },\r\n    methods: {\r\n        updateColor(event) {\r\n            this.state.widgetIconColor = event.target.value;\r\n            // Update the icon preview immediately\r\n            this.$nextTick(() => {\r\n                const iconImage = this.$el?.querySelector('.widget-icon img');\r\n                if (iconImage && this.state.widgetIcon && this.state.widgetIcon.startsWith('si:')) {\r\n                    iconImage.src = this.getIconUrl(this.state.widgetIcon, this.state.widgetIconColor);\r\n                }\r\n            });\r\n        },\r\n        handleUrlInput(event) {\r\n            this.typedUrl = event.target.value;\r\n            this.debounceUrlUpdate();\r\n        },\r\n        debounceUrlUpdate: debounce(function() {\r\n            this.iframeError = false;\r\n            this.state.iframeUrl = this.typedUrl;\r\n        }, 500),\r\n        debounceIconUpdate: debounce(function() {\r\n            this.state.widgetIcon = this.typedIcon;\r\n            this.validateIcon();\r\n        }, 500),\r\n        validateIcon() {\r\n            if (!this.state.widgetIcon || !this.state.widgetIcon.startsWith('si:')) {\r\n                return;\r\n            }\r\n            \r\n            const iconName = this.state.widgetIcon.substring(3).toLowerCase();\r\n            let proxyUrl = generateUrl('/apps/iframewidget/proxy-icon/' + encodeURIComponent(iconName));\r\n            \r\n            if (this.state.widgetIconColor) {\r\n                proxyUrl += '?color=' + encodeURIComponent(this.state.widgetIconColor.replace('#', ''));\r\n            }\r\n            \r\n            axios.get(proxyUrl)\r\n                .then(response => {\r\n                    if (!response.data.exists) {\r\n                        console.warn(`Icon not found: ${iconName}`);\r\n                        showError(t('iframewidget', 'Icon not found: {name}', {name: iconName}));\r\n                    }\r\n                })\r\n                .catch(error => {\r\n                    console.warn(`Icon not found: ${iconName}`);\r\n                    showError(t('iframewidget', 'Icon not found: {name}', {name: iconName}));\r\n                });\r\n        },\r\n        getIconUrl(iconName, color = null) {\r\n            if (!iconName) {\r\n                const prefersDarkMode = window.matchMedia && \r\n                                    window.matchMedia('(prefers-color-scheme: dark)').matches;\r\n                return prefersDarkMode ? \r\n                    OC.filePath('iframewidget', 'img', 'iframewidget-dark.svg') : \r\n                    OC.filePath('iframewidget', 'img', 'iframewidget.svg');\r\n            }\r\n            \r\n            if (iconName.startsWith('si:')) {\r\n                const simpleIconName = iconName.substring(3).toLowerCase();\r\n                \r\n                if (color) {\r\n                    color = color.replace('#', '');\r\n                    return `https://cdn.simpleicons.org/${simpleIconName}/${color}`;\r\n                }\r\n                \r\n                return `https://cdn.simpleicons.org/${simpleIconName}`;\r\n            }\r\n            \r\n            const prefersDarkMode = window.matchMedia && \r\n                                window.matchMedia('(prefers-color-scheme: dark)').matches;\r\n            return prefersDarkMode ? \r\n                OC.filePath('iframewidget', 'img', 'iframewidget-dark.svg') : \r\n                OC.filePath('iframewidget', 'img', 'iframewidget.svg');\r\n        },\r\n        handleIframeError(event) {\r\n            console.warn('Failed to load iframe content: ', event);\r\n            this.iframeError = true;\r\n        },\r\n        handleIconError(event) {\r\n            console.warn(`Icon not found or blocked by CSP: ${event.target.alt}`);\r\n            \r\n            const prefersDarkMode = window.matchMedia && \r\n                window.matchMedia('(prefers-color-scheme: dark)').matches;\r\n            \r\n            const fallbackIcon = prefersDarkMode ? \r\n                OC.filePath('iframewidget', 'img', 'iframewidget-dark.svg') : \r\n                OC.filePath('iframewidget', 'img', 'iframewidget.svg');\r\n            \r\n            event.target.src = fallbackIcon;\r\n        },\r\n        handleCSPViolation(e) {\r\n            if (e.blockedURI && this.state.iframeUrl && \r\n                (e.blockedURI === this.state.iframeUrl || \r\n                this.state.iframeUrl.startsWith(e.blockedURI))) {\r\n                this.iframeError = true;\r\n            }\r\n        },\r\n        clearColor() {\r\n            this.state.widgetIconColor = '';\r\n            this.$forceUpdate();\r\n        },\r\n        async saveSettings() {\r\n            this.saving = true;\r\n            try {\r\n                await axios.post(generateUrl('/apps/iframewidget/personal-settings'), this.state)\r\n                this.originalState = { ...this.state }\r\n                showSuccess(t('iframewidget', 'Settings saved successfully'))\r\n            } catch (e) {\r\n                console.error('Failed to save settings:', e)\r\n                showError(t('iframewidget', 'Failed to save settings'))\r\n            } finally {\r\n                this.saving = false\r\n            }\r\n        }\r\n    }\r\n}\r\n</script>\r\n\r\n<style scoped>\r\n/* Form layout */\r\n.iframewidget-grid-form {\r\n    display: grid;\r\n    margin-top: 30px;\r\n}\r\n\r\n.iframewidget-grid-form label:not(:first-child) {\r\n    margin-top: 20px;\r\n}\r\n\r\ninput {\r\n    max-width: 400px;\r\n    width: 100%;\r\n}\r\n\r\n/* Checkbox styles */\r\n.checkbox-container {\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: flex-start;\r\n}\r\n\r\n.checkbox-container input[type=\"checkbox\"] {\r\n    position: static;\r\n    display: block;\r\n    height: 16px;\r\n    width: 16px;\r\n    margin: 0;\r\n    opacity: 1;\r\n    cursor: pointer;\r\n}\r\n\r\n.checkbox-container .checkbox-icon {\r\n    display: none; \r\n}\r\n\r\n.checkbox-container input[type=\"checkbox\"]:checked {\r\n    background-color: var(--color-primary);\r\n    border-color: var(--color-primary);\r\n}\r\n\r\n/* Button styles */\r\n.iframewidget-button-group {\r\n    margin-top: 20px;\r\n}\r\n\r\n/* Header styles */\r\n.iframewidget-header {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: flex-start;\r\n    margin-bottom: 10px;\r\n}\r\n\r\n.iframewidget-logo {\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n}\r\n\r\n.iframewidget-logo a {\r\n    text-decoration: none;\r\n    color: inherit;\r\n}\r\n\r\n.iframewidget-logo a.logo-link {\r\n    display: block;\r\n}\r\n\r\n.iframewidget-logo a:hover .iframewidget-version {\r\n    text-decoration: underline;\r\n    color: var(--color-primary-element);\r\n}\r\n\r\n.iframewidget-logo-image {\r\n    width: 60px;\r\n    height: 60px;\r\n    border-radius: 5px;\r\n    transition: transform 0.2s ease, box-shadow 0.2s ease;\r\n}\r\n\r\n.iframewidget-logo-image:hover {\r\n    transform: scale(1.05);\r\n    filter: drop-shadow(1px -1px 3px rgba(0, 0, 0, 0.5));\r\n}\r\n\r\n.iframewidget-version {\r\n    font-size: 12px;\r\n    color: var(--color-text-maxcontrast);\r\n    margin-top: 5px;\r\n}\r\n\r\n/* Layout container */\r\n.iframewidget-admin-container {\r\n    display: flex;\r\n    flex-wrap: wrap;\r\n    gap: 20px;\r\n    margin-top: 20px;\r\n}\r\n\r\n.iframewidget-admin-form {\r\n    flex: 0.5;\r\n    min-width: 330px;\r\n}\r\n\r\n.iframewidget-admin-preview {\r\n    flex: 1;\r\n    min-width: 320px;\r\n    max-width: 640px;\r\n}\r\n\r\n/* Preview styles */\r\n.preview-container {\r\n    border: 1px solid var(--color-border);\r\n    border-radius: var(--border-radius-container-large);\r\n    overflow: hidden;\r\n    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);\r\n    background-color: var(--color-main-background-blur);\r\n    transition: width 0.3s ease;\r\n}\r\n\r\n.preview-header {\r\n    display: flex;\r\n    align-items: center;\r\n    padding: 16px;\r\n    background-color: var(--color-main-background-blur);\r\n}\r\n\r\n.preview-header h2 {\r\n    display: block;\r\n    align-items: center;\r\n    flex-grow: 1;\r\n    margin: 0;\r\n    font-size: 20px;\r\n    font-weight: bold;\r\n    padding: 16px 8px;\r\n    height: 56px;\r\n    white-space: nowrap;\r\n    overflow: hidden;\r\n    text-overflow: ellipsis;\r\n}\r\n\r\n.preview-header span:first-child {\r\n    width: 16px;\r\n    height: 16px;\r\n    background-size: 16px;\r\n    margin-right: 8px;\r\n}\r\n\r\n.preview-header span {\r\n    background-size: 32px!important;\r\n    width: 32px!important;\r\n    height: 32px!important;\r\n    background-position: center;\r\n    float: left;\r\n    margin-top: -6px;\r\n}\r\n\r\n.preview-content {\r\n    margin: 0 16px 16px 16px;\r\n    position: relative;\r\n    height: 424px;\r\n}\r\n\r\n.preview-empty {\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    height: 200px;\r\n    padding: 20px;\r\n    color: var(--color-text-maxcontrast);\r\n    text-align: center;\r\n}\r\n\r\n.preview-frame {\r\n    width: 100%;\r\n    height: 100%;\r\n    border: none;\r\n    min-height: 50px;\r\n}\r\n\r\n.preview-title {\r\n    font-size: 1rem;\r\n    font-weight: bold;\r\n    margin-bottom: 10px;\r\n    margin-top: 0;\r\n}\r\n\r\n/* Empty title styles */\r\n.preview-header.preview-title-empty {\r\n    height: 10px !important;\r\n    padding: 0 !important;\r\n}\r\n\r\n.preview-header.preview-title-empty h2 {\r\n    display: none !important;\r\n}\r\n\r\n.preview-content.preview-title-empty {\r\n    height: calc(424px + 78px);\r\n}\r\n\r\n/* Custom icon styles */\r\n.preview-header h2 .widget-icon {\r\n    float: left !important;\r\n    display: inline-flex !important;\r\n    margin-right: 16px !important;\r\n    margin-left: 0 !important;\r\n    position: relative !important;\r\n    width: 32px !important;\r\n    height: 32px !important;\r\n}\r\n\r\n.preview-header h2 .widget-icon img {\r\n    width: 32px;\r\n    height: 32px;\r\n    object-fit: contain;\r\n    vertical-align: middle;\r\n}\r\n\r\n/* Hide default icon when custom icon is present */\r\n.preview-header h2 .widget-icon + .icon-iframewidget {\r\n    display: none !important;\r\n}\r\n\r\n/* Icon input styles */\r\n.icon-input-container {\r\n    display: flex;\r\n    align-items: center;\r\n    gap: 15px;\r\n    max-width: 400px!important;\r\n}\r\n\r\n/* Icon Finder Link styles */\r\n.icon-finder {\r\n    font-size: 0.85em;\r\n    font-weight: normal;\r\n    color: var(--color-text-maxcontrast);\r\n}\r\n\r\n.icon-finder a {\r\n    color: var(--color-primary);\r\n}\r\n\r\n/* Color button container transitions */\r\n.color-button-container {\r\n    display: inline-flex;\r\n    transition: width 0.3s ease, margin 0.3s ease;\r\n    width: 0;\r\n    height: 32px;\r\n}\r\n\r\n.color-button-container.has-button {\r\n    width: 32px;\r\n    margin-left: 4px;\r\n}\r\n\r\n/* Color picker styles */\r\ninput[type=\"color\"] {\r\n    aspect-ratio: 1/1;\r\n    min-width: 32px;\r\n    min-height: 32px;\r\n    width: 32px;\r\n    height: 32px;\r\n    padding: 0!important;\r\n    margin: 0!important;\r\n    border-radius: 50%;\r\n    box-shadow: 0 0 5px rgba(255, 255, 255, 0.5) !important;\r\n    transition: transform 0.2s, box-shadow 0.2s;\r\n    cursor: pointer!important;\r\n}\r\n\r\ninput[type=\"color\"]:hover {\r\n    transform: scale(1.1);\r\n    transition: transform 0.2s, box-shadow 0.2s;\r\n    box-shadow: 0 0 12px rgba(255, 255, 255, 0.5) !important;\r\n}\r\n\r\ninput[type=\"color\"]::-webkit-color-swatch-wrapper {\r\n    padding: 0;\r\n}\r\n\r\ninput[type=\"color\"]::-webkit-color-swatch {\r\n    border: none;\r\n    border-radius: 50%;\r\n}\r\n\r\n/* Firefox support */\r\ninput[type=\"color\"]::-moz-color-swatch {\r\n    border: none;\r\n    border-radius: 50%;\r\n}\r\n\r\n/* Color reset button transitions */\r\n.icon-reset-color {\r\n    min-width: 32px;\r\n    min-height: 32px;\r\n    background-color: transparent;\r\n    border: none;\r\n    cursor: pointer;\r\n    opacity: 0.7;\r\n    margin: 0!important;\r\n    border-radius: 50%;\r\n}\r\n\r\n.icon-reset-color:hover {\r\n    opacity: 1;\r\n    background-color: var(--color-background-hover);\r\n}\r\n\r\n.fade-scale-enter-from {\r\n    opacity: 0;\r\n    transform: scale(0);\r\n}\r\n\r\n.fade-scale-enter-active, \r\n.fade-scale-leave-active {\r\n    transition: all 0.3s ease;\r\n}\r\n\r\n.fade-scale-enter-from,\r\n.fade-scale-leave-to {\r\n    opacity: 0;\r\n    transform: scale(0);\r\n}\r\n\r\n.icon-preview {\r\n    display: inline-flex;\r\n    align-items: center;\r\n    margin: 0 4px;\r\n}\r\n\r\n/* CSP error display styling */\r\n.widget-error.csp-error {\r\n    display: flex;\r\n    flex-direction: column;\r\n    padding: 16px;\r\n    text-align: center;\r\n    height: 200px;\r\n    justify-content: center;\r\n}\r\n\r\n/* Error title styling */\r\n.widget-error .error-title {\r\n    font-weight: bold;\r\n    margin-bottom: 8px;\r\n    color: var(--color-text-maxcontrast);\r\n}\r\n\r\n/* Error message paragraph styling */\r\n.widget-error p {\r\n    margin: 8px 0;\r\n    color: var(--color-text-maxcontrast);\r\n}\r\n\r\n/* Container for action buttons in error state */\r\n.error-actions {\r\n    margin-top: 16px;\r\n}\r\n\r\n/* Button styling for error actions */\r\n.error-actions .button {\r\n    display: inline-block;\r\n    padding: 8px 16px;\r\n    text-decoration: none;\r\n    border-radius: var(--border-radius);\r\n    background-color: var(--color-primary);\r\n    color: var(--color-primary-text);\r\n}\r\n\r\n/* Hover state for action buttons */\r\n.error-actions .button:hover {\r\n    background-color: var(--color-primary-element-hover);\r\n}\r\n\r\n</style>\r\n"],"sourceRoot":""}]);
